<template>
  <div style="position: relative;">
    <search :keyword='keyword'></search>
    <div class="top_tabs">
      <div :class="sort==0?'top_tabs_activity':''" @click="synthesize_sort">
        <div>综合排序</div>
      </div>
      <div :class="sort==2||sort==3?'top_tabs_activity':''" @click="price_sort">
        <div>价格排序</div>
        <div class="triangle_box">
          <span class="triangle_box_0" :style="sort==2?'border-bottom-color:#ed2f26':''"></span>
          <span class="triangle_box_1" :style="sort==3?'border-top-color:#ed2f26':''"></span>
        </div>
      </div>
      <div @click="lookClassify">
        <div>分类查看</div>
      </div>
    </div>
    <div class="top_tabs_placeholder"></div>
    <div :class="classifyShow ?'category_box_activity category_box':'category_box'">
      <!-- 一级分类 -->
      <div class="main_category_box">
        <div>
          <div :class="'main_category' + ' ' + touchstartA" @touchend="all_category_click" @touchstart="touchstartAll">全部</div>
        </div>
        <div v-for="(item,index) in categoryList" :key="index">
          <div :class="index == checkedClassify_0? (categoryActive?categoryActive:'main_category main_category_activity'):'main_category'" @touchend="main_category_click(index)" @touchstart="touchstart(index)">{{item.label}}</div>
        </div>
      </div>
      <!-- 子分类 -->
      <div class="sub_category_box" v-if="categoryList.length>0 && checkedClassify_0 != null">
        <div class="sub_category_box_0" v-for="(item,index) in categoryList[checkedClassify_0].children" :key="index">
          <div :class="index == checkedClassify_1?'sub_category_box_0_name sub_category_box_0_name_activity':'sub_category_box_0_name'" @click="sub_category_click(index,null)">{{item.label}}</div>
          <div class="sub_category_box_1">
            <div v-for="(item,idx) in item.children" :key="idx">
              <div :class="index == checkedClassify_1 && idx == checkedClassify_2?'sub_category_box_1_name sub_category_box_1_name_activity':'sub_category_box_1_name'" @click="sub_category_click(index,idx)">
              {{item.label}}
              </div>
            </div>
          </div>
        </div>
        <div class="placeholder_box"></div>
        <div class="category_confirm_box">
          <div class="category_cancel_btn" @click="closeClassifyPop">取消</div>
          <div class="category_confirm_btn" @click="category_confirm_fn">确认</div>
        </div>
      </div>
    </div>
    
    <!--商品列表-->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="van-pull-refresh_margin">
      <van-list
        v-model="loading"
        :finished="finished"
        :finished-text="goodsDataList.length == 0 ? '' :'没有更多了'"
        @load="onLoad"
      >
        <div class="activity_box">
          <div v-for="(item,index) in goodsDataList" :key="index" class="goods_box">
            <div class="goods_img_box" @click="toDetail(item)">
              <img v-lazy='item.imgURl' />
              <div v-if="item.store == 0">已缺货</div>
              <!-- <span class="consell-tag" v-if="item.consell & 1 == 1">控销药</span> -->
              <img class="consell-tag" v-if="(item.consell & 2) > 0" src="..\..\assets\consell-tag-1.png" />
              <img class="consell-tag" v-else-if="(item.consell & 1) > 0" src="..\..\assets\consell-tag-0.png" />
            </div>
            <div class="goods_info">
              <div class="goods_title">{{item.prodname}}<span>{{item.spec}}</span></div>
              <div class="goods_supplier">{{item.manuName}}</div>
              
              <div v-if="(item.ladOffDesc && item.ladOffDesc.length>0)||((item.rulestatus & 2048) > 0)||((item.rulestatus & 4096) > 0)" class="goods-activity-description padding-l-r-20">
                <div v-if="item.ladOffDesc && item.ladOffDesc.length>0" style="margin-right: 4px;">{{item.ladOffDesc[0]}}</div>
                <div v-if="item.isneareffect" style="margin-right: 4px;">近效期</div>
                <div v-if="(item.rulestatus & 2048) > 0" style="margin-right: 4px;">秒杀</div>
                <div v-if="(item.rulestatus & 4096) > 0">团购</div>
              </div>
              
              <div class="goods_tag_box">
                
                <!-- <div class="goods_tag" v-if="item.rulestatus.length>0">{{item.rulestatus[0]}}</div> -->
                <div class="goods_spe">中包装：{{item.medpacknum}}{{item.unitName}}</div>
              </div>
              <div class="goods_price_box">
                <div class="goods_price" v-if="item.purchaseprice != -1 && item.purchaseprice != -2">
                  <div class="actual_price">￥{{item.purchaseprice}}</div>
                  <div class="original_price_0" v-if="item.cart>0||item._showInput">...</div>
                  <div class="original_price" v-else>￥{{item.mp}}</div>
                </div>
                <div class="goods_price" v-else>
                  <div class="actual_price" v-if="item.purchaseprice == -1">￥认证后可见</div>
                  <div class="actual_price" v-if="item.purchaseprice == -2">￥签约后可见</div>
                </div>
                <div class="goods_num">
                  <div class="goods_num_minus" :style="item.cart>0||item._showInput?'display:block':'display:none'" @click="fn_minus(item)">-</div>
                  <input type="number" v-model="item.cart" @blur="fn_blur(item)" @focus="fn_focus(item)" :style="item.cart>0||item._showInput?'display:block':'display:none'" />
                  <div class="goods_num_add" @click="fn_add(item)">+</div>
                </div>
              </div>
              <div class="goods_count_tag" v-if="item.purchaseprice != -1 && item.purchaseprice != -2">
                <div>毛利率  {{item.grossProfit}}%</div>
                <div>零售价  {{item.rrp}}</div>
              </div>
              
            </div>
          </div>
        </div>
        
      </van-list>
      <div class="no_goods" v-if="finished && goodsDataList.length == 0"><img src="../../assets/noGoods.png" /></div>
    </van-pull-refresh>
  </div>
</template>
<script>
import * as types from '@/store/mutations'
import {splitRoleID} from './../../utils/activity.js'
import Search from "@/components/Search.vue";

export default {
  components: {
    Search
  },
  data() {
    return {
      isLoading:false,//上拉刷新
      loading:true,//下拉加载
      finished:false,//可以下拉加载
      pageIndex:1,//页码
      keyword:'',//关键字
      sort:0,//排序方式，综合0，销量1，价格升2，价格降3
      classifyShow:false,
      categoryList: [],
      goodsDataList:[],
      checkedClassifyArr:[null,null,null],//需要查询的分类
      checkedClassify_0:null,//当前选中一级分类下标
      checkedClassify_1:null,//当前选中二级分类下标
      checkedClassify_2:null,//当前选中三级分类下标
      homeTop: 0,
      categoryActive: '',
      touchstartA: ''
    };
  },
  created() {
    this.$store.commit(types.SET_TABBAR_INDEX,1)
  },
  mounted() {
    // let classifiData = sessionStorage.getItem("classifiData");
    // if(classifiData){
    //   classifiData = JSON.parse(classifiData);
    //   this.scrollTop = classifiData.scrollTop;
    //   this.keyword = classifiData.keyword;
    //   this.pageIndex = classifiData.pageIndex;
    //   this.checkedClassifyArr = classifiData.checkedClassifyArr;
    //   this.checkedClassify_0=classifiData.checkedClassify_0;
    //   this.checkedClassify_1=classifiData.checkedClassify_1;
    //   this.checkedClassify_2=classifiData.checkedClassify_2;
    //   this.sort=classifiData.sort;
    //   //清除缓存
    //   sessionStorage.removeItem("classifiData");
    //   this.loading = true;
    //   this.getClassifyData(true);
    //   //this.getGoodsListData(true);
    // }else{
    //   this.keyword = this.$route.query.keyword;
    //   this.loading = true;
    //   this.getClassifyData(false);
    //   //this.getGoodsListData();
    // }
  },
  activated() {
    this.goodsDataList = [];
    this.finished=false;
    let classifiData = sessionStorage.getItem("classifiData");
    if(classifiData){
      classifiData = JSON.parse(classifiData);
      this.scrollTop = classifiData.scrollTop;
      this.keyword = classifiData.keyword;
      this.pageIndex = classifiData.pageIndex;
      this.checkedClassifyArr = classifiData.checkedClassifyArr;
      this.checkedClassify_0=classifiData.checkedClassify_0;
      this.checkedClassify_1=classifiData.checkedClassify_1;
      this.checkedClassify_2=classifiData.checkedClassify_2;
      this.sort=classifiData.sort;
      //清除缓存
      sessionStorage.removeItem("classifiData");
      this.loading = true;
      this.getClassifyData(true);
      //this.getGoodsListData(true);
    }else{
      this.keyword = this.$route.query.keyword;
      this.loading = true;
      this.getClassifyData(false);
      //this.getGoodsListData();
    }
    //this.getClassifyData(false);
    //this.getGoodsListData(true)
    // this.$nextTick(() => {
    //   document.documentElement.scrollTop = this.homeTop || 0;
    // });
  },
  // beforeRouteLeave(to, from, next) {
  //   this.homeTop = document.documentElement.scrollTop || 0;
  //   next();
  // },
  methods: {
    //综合排序
    synthesize_sort(){
      this.sort = 0;
      //查询商品信息
      this.pageIndex = 1;
      this.finished = false;//解除下拉加载锁定
      this.loading = true;
      this.goodsDataList = [];//清空原有商品数据
      if(this.classifyShow){
        this.closeClassifyPop();
      }
      this.getGoodsListData();
    },
    //价格排序
    price_sort(){
      if(this.sort == 0 || this.sort == 3){
        this.sort = 2
      }else{
        this.sort = 3
      }
      //查询商品信息
      this.pageIndex = 1;
      this.finished = false;//解除下拉加载锁定
      this.loading = true;
      this.goodsDataList = [];//清空原有商品数据
      if(this.classifyShow){
        this.closeClassifyPop();
      }
      this.getGoodsListData();
    },
    //查看分类
    lookClassify(){
      //保存原分类
      this.checkedClassifyArr=[this.checkedClassify_0,this.checkedClassify_1,this.checkedClassify_2];
      //设置默认选中一级分类
      if(this.checkedClassify_0 == null){
        this.checkedClassify_0 = 0
      }
      this.classifyShow = !this.classifyShow;
    },
    //全部分类
    all_category_click(){
      this.touchstartA = ''
      this.checkedClassify_0 = null;
      this.checkedClassify_1 = null;//当前选中二级分类下标
      this.checkedClassify_2 = null;//当前选中三级分类下标
      this.checkedClassifyArr = [null,null,null];
      this.classifyShow = false;
      //查询商品信息
      this.pageIndex = 1;
      this.finished = false;//解除下拉加载锁定
      this.loading = true;
      this.goodsDataList = [];//清空原有商品数据
      this.getGoodsListData();
    },
    touchstartAll() {
      this.touchstartA = 'touchstartA'
    },
    //主分类点击
    main_category_click(index){
      this.checkedClassify_1 = null;//当前选中二级分类下标
      this.checkedClassify_2 = null;//当前选中三级分类下标
      this.categoryActive = ''
    },
    //子分类点击
    sub_category_click(index,idx){
      this.checkedClassify_1 = index;//当前选中二级分类下标
      this.checkedClassify_2 = idx;//当前选中三级分类下标
      this.category_confirm_fn();
    },
    //关闭分类列表
    closeClassifyPop(){
      //恢复原分类
      this.checkedClassify_0 = this.checkedClassifyArr[0];
      this.checkedClassify_1 = this.checkedClassifyArr[1];//当前选中二级分类下标
      this.checkedClassify_2 = this.checkedClassifyArr[2];//当前选中三级分类下标
      this.classifyShow = false;
    },
    //确认分类
    category_confirm_fn(){
      this.checkedClassifyArr = [this.checkedClassify_0,this.checkedClassify_1,this.checkedClassify_2];
      this.classifyShow = false;
      //查询商品信息
      this.pageIndex = 1;
      this.finished = false;//解除下拉加载锁定
      this.loading = true;
      this.goodsDataList = [];//清空原有商品数据
      this.getGoodsListData();
    },
    onRefresh(){//上拉刷新
      this.pageIndex = 1;
      this.getGoodsListData();
    },
    onLoad(){//下拉加载
      this.getGoodsListData();
    },
    //商品减少
    fn_minus(obj){
      this.$manageGoodNum(this,obj,-1);
    },
    //商品添加
    fn_add(obj){
      this.$manageGoodNum(this,obj,-2);
    },
    //输入商品数量
    fn_blur(obj){
      obj._showInput = false;
      if(obj.cart == ''){
        obj.cart = 0;
      }
      this.$manageGoodNum(this,obj,obj.cart);
    },
    //获取焦点时一直显示input框
    fn_focus(obj){
      obj._showInput = true;
    },
    //获取商品分类信息
    getClassifyData(hasCache){
      JNB.query({
        serverName: "globalServer",
        cls: "CommonModule",
        method: "getProduceClasses",
        callback :(data) => {
          data = JSON.parse(data);
          data.data = JSON.parse(data.data);
          this.categoryList = data.data;
          if(!hasCache){
            //如果是从首页楼层进入则循环获取下标并设置保存this.checkedClassifyArr中
            if(this.$route.query.spuCodes){
              for(let i = 0;i<this.categoryList.length;i++){
                if(this.$route.query.spuCodes == this.categoryList[i].value){
                  this.checkedClassifyArr[0] = i;
                  this.checkedClassifyArr[1] = null;
                  this.checkedClassifyArr[2] = null;
                  this.checkedClassify_0 = i;
                  this.checkedClassify_1 = null;
                  this.checkedClassify_2 = null;
                }
                for(let j = 0; j<this.categoryList[i].children.length; j++){
                  if(this.$route.query.spuCodes == this.categoryList[i].children[j].value){
                    this.checkedClassifyArr[0] = i;
                    this.checkedClassifyArr[1] = j;
                    this.checkedClassifyArr[2] = null;
                    this.checkedClassify_0 = i;
                    this.checkedClassify_1 = j;
                    this.checkedClassify_2 = null;
                  }
                  for(let k = 0; k<this.categoryList[i].children[j].children.length; k++){
                    if(this.$route.query.spuCodes == this.categoryList[i].children[j].children[k].value){
                      this.checkedClassifyArr[0] = i;
                      this.checkedClassifyArr[1] = j;
                      this.checkedClassifyArr[2] = k;
                      this.checkedClassify_0 = i;
                      this.checkedClassify_1 = j;
                      this.checkedClassify_2 = k;
                    }
                  }
                }
              }
            }
          }
          this.getGoodsListData(hasCache)
        }
      })
    },
    //获取商品数据
    getGoodsListData(hasCache){
      let spu = 0;
      if(this.checkedClassifyArr[2] != null){
        spu = this.categoryList[this.checkedClassifyArr[0]].children[this.checkedClassifyArr[1]].children[this.checkedClassifyArr[2]].value;
      }else if(this.checkedClassifyArr[1] != null){
        spu = this.categoryList[this.checkedClassifyArr[0]].children[this.checkedClassifyArr[1]].value;
      }else if(this.checkedClassifyArr[0] != null){
        spu = this.categoryList[this.checkedClassifyArr[0]].value;
      }
      let data={
        keyword: this.keyword,//关键字
        specArray: [],//规格
        manuArray: [],//厂家
        brandArray: [],//品牌
        spu: spu,//分类数据
        sort: this.sort,//排序方式，综合0，销量1，价格升2，价格降3
      }
      JNB.query({
        serverName: "goodsServer",
        cls: "ProdModule",
        method: "fullTextsearchProdMall",
        params:data,
        page:hasCache?1:this.pageIndex,
        count:hasCache?this.pageIndex*10:10,
        callback :(data) => {
          data = JSON.parse(data);
          console.log(data)
          if(data.code == 200){
            this.isLoading = false;//如果是上拉刷新就停止刷新
            this.loading = false;//加载完成
            //处理商品数据
            if(data.data && data.data.length>0){
              data.data.forEach(item => {
                item.cart = 0;//设置初始购物车数量
              })
              this.$request_img({//获取商品图片
                context: this,
                arr: data.data
              })
            }
            if(data.pageNo == 1){
              this.goodsDataList = data.data;
            }else{
              this.goodsDataList = this.goodsDataList.concat(data.data);
            }
            if(hasCache){
              if(data.total/data.pageSize<=1){
                this.finished = true;//锁定下拉加载
              }else{
                this.pageIndex = this.pageIndex+1;
              }
              setTimeout(() =>{
                document.documentElement.scrollTop = this.scrollTop;
              },100)
            }else{
              if(data.total/data.pageSize<=this.pageIndex){
                this.finished = true;//锁定下拉加载
              }else{
                this.pageIndex = this.pageIndex+1;
              }
            }
          }
        }
      })
    },
    //商品详情跳转
    toDetail(obj){
      //缓存回到此页面时需要的信息
      let data = {
        scrollTop:document.documentElement.scrollTop || 0,//滚动条位置
        pageIndex:this.pageIndex,//页码
        keyword:this.keyword,//关键字
        checkedClassifyArr:this.checkedClassifyArr,//排序方式
        checkedClassify_0:this.checkedClassify_0,//排序方式,当前选中一级分类下标
        checkedClassify_1:this.checkedClassify_1,//排序方式,当前选中二级分类下标
        checkedClassify_2:this.checkedClassify_2,//排序方式,当前选中三级分类下标
        sort:this.sort,//选中的分类
      }
      data = JSON.stringify(data);
      sessionStorage.setItem(
        "classifiData",
        data
      );
      this.$router.push({
        path: '/goodsDetail',
        query:{
          sku:obj.sku
        }
      })
    },
    //是否有路由参数
    //有参数根据参数调用商品搜索接口
    //无参数将关键字置空调用商品搜索接口
    toPages(name) {
      this.$router.push({
        name: name
      });
    },
    touchstart(index) {
      this.checkedClassify_0 = index;
      this.categoryActive = 'categoryActive'
    }
  }
}
</script>

<style scoped>
  .padding-l-r-20{
    padding: 0 20px;
  }
  .goods-activity-description{
    margin-top: 10px;
    display: flex;
    overflow: hidden;
  }
  .goods-activity-description div{
    background: #fff5f5;
    padding:0 20px;
    border-radius: 6px;
    border: 1px solid #f62929;
    height: 32px;
    color: #f62929;
    font-size: 20px;
    line-height: 32px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .no_goods img{
    width: 375px;
    /* height: 362px;
    width: 269px; */
    display: block;
    margin: 100px auto 0;
  }
  .top_tabs{
    border-top: 1px solid #f5f5f5;
    height: 74px;
    display: flex;
    border-bottom: 1px solid #f5f5f5;
    background-color: #FFFFFF;
    position: fixed;
    left: 0;
    right: 0;
    top: 90px;
    z-index: 11;
    color: #666;
  }
  .top_tabs_placeholder{
    height: 90px;
  }
  .top_tabs .top_tabs_activity{
    color: #333333;
    border-bottom: 4px solid #ED2F26;
  }
  .top_tabs>div{
    flex: 1;
    text-align: center;
    line-height: 70px;
    font-size: 28px;
    height: 70px;
    border-bottom: 4px solid #FFFFFF;
    display: flex;
    margin: 0 50px;
    justify-content: center;
  }
  .triangle_box{
    margin-left: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .triangle_box_0{
    display: block;
    width: 0;
    height: 0;
    border-top: 10px transparent dashed;
    border-left: 10px transparent dashed;
    border-bottom: 10px #000 dashed;
    border-right: 10px transparent solid;
    margin-bottom: 4px;
  }
  .triangle_box_1{
    display: block;
    width: 0;
    height: 0;
    border-top: 10px  #000 dashed;
    border-left: 10px transparent dashed;
    border-bottom: 10px transparent dashed;
    border-right: 10px transparent solid;
  }
  .category_box{
    position: fixed;
    background-color: #ffffff;
    width: 750px;
    z-index: 10;
    display: flex;
    height: calc(100% - 266px);
    top: -1500px;
    transition: top 0.5s;
  }
  .category_box_activity{
    top: 167px;
    transition: top 0.5s;
  }
  .main_category_box{
    /* border-right: 1px solid #eeeeee; */
    height: 100%;
    overflow: scroll;
  }
  .main_category{
    height: 70px;
    line-height: 70px;
    font-size: 20px;
    width: 100px;
    padding:0 10px;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    /* border-bottom: 1px solid #EEEEEE; */
    color: #666;
  }
  .touchstartA{
    font-size: 32px;
    box-shadow: 0 0px 20px rgba(10,16,20,.34),0 0 8px rgba(10,16,20,.32);
    color: #ED2F26;
  }
  .main_category_activity{
    background-color: #F5F5F5;
    color: #ED2F26;
  }
  .categoryActive{
    font-size: 20px;
    height: 70px;
    width: 100px;
    line-height: 70px;
    text-align: center;
    padding:0 10px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    box-shadow: 0 0px 20px rgba(10,16,20,.24),0 0 8px rgba(10,16,20,.12);
    /* border:1px solid #ff6700; */
    color: #ED2F26;
  }
  .sub_category_box{
    width: calc(100% - 120px);
    height: 100%;
    background-color: #f5f5f5;
    overflow: scroll;
  }
  .sub_category_box_0{
    padding: 30px 30px 10px;
    border-bottom: 1px solid #EEEEEE;
  }
  .sub_category_box_0_name{
    color: #333333;
    font-weight: bold;
    font-size: 28px;
    height: 34px;
    line-height: 34px;
    padding-bottom: 30px;
  }
  .sub_category_box_0_name_activity{
    color: #FF0000;
  }
