      <div class="icon_box" @click="scan">
        <van-icon name="scan" size="30px" />
      </div>
      <input placeholder="药品名、通用名、生产厂家" readonly="readonly" @click="toPages('search')" v-model="keyword" />
      <div class="search_btn" @click="toPages('message')">
        <img src="../assets/me_icon/xiaoxi.png" style="" />
        <div v-if="isExistMessage"></div>
      </div>
    </div>
    <div class="placeholder"></div>
  </div>
</template>

<script>
  export default {
    props:['keyword'],
    data() {
      return {
        
      }
    },
    computed: {
      isExistMessage(){
        return this.$store.state.message.length >0
      }
    },
    mounted(){
      
    },
    methods:{
      toPages(name) {
        this.$router.push({
          path: '/'+name,
          query: {
            keyword:this.keyword
          }
        });
      },
      scan(){
        JNB._requestNative("scan",(data) => {
          alert("敬请期待")
        })
      }
    }
  }
</script>

<style scoped>
  .search_box{
    background-color: #FFFFFF;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 90px;
    display: flex;
    align-items: center;
  }
  .icon_box{
    height: 90px;
    width: 85px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #999999;
  }
  .search_box input{
    display: block;
    border-radius:28px;
    height: 58px;
    border: none;
    flex: 1;
    background-color: #F0F0F0;
    padding-left: 50px;
    font-size: 28px;
  }
  .search_btn{
    width: 100px;
    text-align: center;
    line-height: 90px;
